<template>
  <div class="payResult mescroll">
    <div class="top">
      <div style="height:25vh;">
        <div v-if="$route.query.type == '1'">
          <img src="../../assets/img/activity/success.png" alt />
          <p class="state">支付成功</p>
          <p class="text">我们尽快安排发货，请买家保持手机通讯畅通</p>
          <p class="text">以便快递哥哥第一时间联系到您。</p>
        </div>
        <div v-if="$route.query.type == '2'">
          <img src="../../assets/img/activity/cancel.png" alt />
          <p class="state">支付取消</p>
        </div>
        <div v-if="$route.query.type == '3'">
          <img src="../../assets/img/activity/fail.png" alt />
          <p class="state">支付失败</p>
        </div>
      </div>
      <div class="btns">
        <div class="btn">
          <div class="btnContent" @click="toIndex">去首页</div>
        </div>
        <div class="btn">
          <div class="btnContent" @click="share">去分享</div>
        </div>
      </div>

    </div>
    <div class="mid">
      <div class="line" @click="toShop" v-if="shopList.length">
        <div class="left">去{{shopDetail.shopName}}的小店看看</div>
        <div class="right">
          <van-icon name="arrow" />
        </div>
      </div>
      <div class="list" :style="shopList.length?'':'padding:0'">
        <div class="item" v-for="(item,index) in shopList" :key="index" :style="index%2==0?'margin-right:10px;':''" @click="goDetail(item.id)">
          <img class="pic" :src="item.gimg?$utils.dealImg(item.gimg):require('../../assets/img/default/goodsDemo.jpg')" alt="">
          <p class="name">{{item.gname}}</p>
          <p class="price">
            <span class="symbol">￥</span>
            <span class="num">{{item.goutPrice.toFixed(2)}}</span>
          </p>
        </div>
      </div>
      <div class="technical" v-if="false">
        <img class="logo" src="../../assets/img/activity/logo.png" alt="">
        <p class="title">本活动由“爆款王”提供技术支持</p>
        <div class="flex" @click="add">
          <img class="icon" src="../../assets/img/activity/icon.png" alt="">
          <p class="text">
            我也要推爆款
            <span>》》</span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Icon } from "vant";
import { getShopTopGoods } from "@/api";
export default {
  components: {
    [Icon.name]: Icon,
  },
  data() {
    return {
      shopDetail: {},
      shopList: [],
    };
  },
  methods: {
    toIndex() {
      this.$router.replace({
        name: "activityIndex",
      });
    },
    share() {
      this.$router.back();
    },
    toShop() {
      this.$router.replace({
        name: "merchantsIndex",
        query: {
          shopCode: this.shopDetail.shopCode,
        },
      });
    },
    goDetail(id) {
      this.$router.replace({ name: "goodsDetail", query: { id: id } });
    },
    add() {
      this.$router.replace({ name: "addActivity" });
    },
    getDate(time) {
      let year = new Date(time).getFullYear(),
        month = new Date(time).getMonth() + 1,
        day = new Date(time).getDate(),
        hour = new Date(time).getHours(),
        min = new Date(time).getMinutes(),
        sec = new Date(time).getSeconds();
      return (
        year +
        "-" +
        (month < 10 ? "0" + month : month) +
        "-" +
        (day < 10 ? "0" + day : day) +
        " " +
        (hour < 10 ? "0" + hour : hour) +
        ":" +
        (min < 10 ? "0" + min : min) +
        ":" +
        (sec < 10 ? "0" + sec : sec)
      );
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getShopTopGoods({
        shopcode: this.$route.query.shopCode,
        i: 6,
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.shopDetail = res.data.shop;
          this.shopList = res.data.goodsList;
        } else {
          this.$toast.fail(res.msg);
        }
      });
    },
  },
  mounted() {
    document.title = "支付结果";
    this.loadData();
  },
};
</script>
<style lang="scss" scoped>
.payResult {
  background: #f4f4f4;
  .top {
    background: url("../../assets/img/activity/bg2.png") no-repeat;
    background-size: 100% 100%;
    text-align: center;
    height: 65vh;
    img {
      width: 60px;
      height: 60px;
      margin: 15vh 0 0;
    }
    .price {
      color: $base-color;
      font-weight: bold;
      font-size: 30px;
      padding: 5px 0;
      span {
        font-size: 18px;
        margin-right: 5px;
      }
    }
    .state {
      font-weight: bold;
      line-height: 30px;
    }
    .text {
      font-size: 13px;
      color: #999;
      line-height: 20px;
    }
    .orderInfo {
      margin: 50px 0 0;
      line-height: 35px;
      padding: 0 20px;
      font-size: 15px;
      .flex {
        display: flex;
        align-items: center;
        .flex-1 {
          width: 100px;
          color: #999;
          text-align: left;
        }
        .flex-2 {
          flex: 2;
          text-align: right;
        }
      }
    }
    .btns {
      margin: 25vh 8vw 0;
      display: flex;
      .btn {
        flex: 1;
        .btnContent {
          width: 30vw;
          margin: 0 auto;
          background: $base-color;
          border-radius: 5px;
          height: 40px;
          line-height: 40px;
          color: #fff;
          font-size: 15px;
        }
      }
    }
  }
  .mid {
    .line {
      display: flex;
      align-items: center;
      background: #fff;
      padding: 10px;
      color: #eb6464;
      font-weight: bold;
      .left {
        flex: 2;
      }
      .right {
        width: 20px;
      }
    }
    .list {
      padding: 10px;
      .item {
        background: #fff;
        width: calc((100% - 10px) / 2);
        display: inline-block;
        margin: 0 0 10px;
        padding: 0 0 10px;
        img {
          width: 100%;
          // height: calc((100vw - 10px) / 2 / 1.316);
        }
        .name {
          padding: 0 5px;
          font-size: 14px;
          line-height: 20px;
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .price {
          padding: 0 5px;
          color: #eb6464;
          .symbol {
            font-size: 12px;
          }
          .num {
            font-size: 18px;
            font-weight: bold;
          }
        }
      }
    }
    .technical {
      margin: 10px 0 20px;
      text-align: center;
      .logo {
        width: 30px;
        margin-bottom: 10px;
      }
      .title {
        color: #666;
        font-size: 14px;
        line-height: 30px;
      }
      .flex {
        display: flex;
        align-items: center;
        justify-content: center;
        .icon {
          width: 25px;
          margin-right: 5px;
        }
        .text {
          line-height: 30px;
          font-size: 15px;
          color: #f56873;
          border-bottom: 1pt solid #f56873;
          font-weight: bold;
          span {
            margin-left: 5px;
            letter-spacing: -5px;
          }
        }
      }
    }
  }
}
</style>
